
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
;
import sha512 from 'js-sha512' //sha512
export default {
	data() {
		return {
			oldPassword: "",
			password: '',
			password2: ''
		}
	},
	computed: {
		//
		isEqually() {
			return this.password == this.password2 ? true : false
		}
	},
	methods: {
		//
		editPassword() {
			if(this.isEqually) {
				this.$vux.confirm.show({
					title: '',
					content: '',
					onConfirm: () => {
						this.$common.ajax({
							data: {
								header: {
									action: 'EditDoctorPassword',
								},
								body: {
									doctorid: this.$common.getLocalStorage('user').doctorid,
									oldpassword: sha512(this.oldPassword),
									newpassword: sha512(this.password)
								}
							},
							success: (res) => {
								this.$vux.toast.show({
									text: '',
									onHide: () => {
										this.$router.go(-1);
									}
								});
							}
						})
					}
				})
			} else {
				this.$vux.toast.show({
					text: '',
					type: 'text'
				});
			}
		}
	},
	mounted() {
		this.$validator.validateAll()
	},
	components: {
		XInput,
		Group,
		XButton,
		XHeader
	}
}



